import {
  GET_ROOM,
  FILTERED_ROOMS,
} from './types';
import items from "./data";

const formatData = items => {
  let tempItems = items.map(item => {
    let id = item.sys.id;
    let images = item.fields.images.map(image => image.fields.file.url);

    let room = { ...item.fields, images, id };
    return room;
  });
  return tempItems;
}

let rms = formatData(items);
let mxpr = Math.max(...rms.map(item => item.price));
const initialState = {
  rooms: rms,
  sortedRooms: rms,
  featuredRooms: rms.filter(room => room.featured === true),
  loading: true,
  type: "all",
  capacity: 1,
  minPrice: 0,
  maxPrice: mxpr,
  minSize: 0,
  maxSize: Math.max(...rms.map(item => item.size)),
  price: mxpr,
  breakfast: false,
  pets: false
};




const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_ROOM:
      let tempRooms = [state.rooms];
      const room = tempRooms[0].find(room => room.slug === action.payload);
      return {
        ...state,
        room,
        loading: false
      };
    case FILTERED_ROOMS:
      let tempRms = [...state.rooms];

    // transform values
    // get capacity
    let capacity = isNaN(parseInt(action.payload.capacity)) ? 0 : parseInt(action.payload.capacity);
    let price = isNaN(parseInt(action.payload.price)) ? 0 : parseInt(action.payload.price);

    // filter by type
    if (action.payload.type !== "all") {
      tempRms = tempRms.filter(room => {
        return room.type === action.payload.type});
    }

    // filter by capacity
    if (capacity !== 1) {
      tempRms = tempRms.filter(room => room.capacity >= capacity);
    }

    // filter by price
    if(price){
      tempRms = tempRms.filter(room => room.price <= price);
    }

    //filter by size
    if(action.payload.minSize && action.payload.maxSize){
      tempRms = tempRms.filter(
        room => room.size >= action.payload.minSize && room.size <= action.payload.maxSize
      );
    }

    //filter by breakfast
    if (action.payload.breakfast) {
      tempRms = tempRms.filter(room => room.breakfast === true);
    }

    //filter by pets
    if (action.payload.pets) {
      tempRms = tempRms.filter(room => room.pets === true);
    }
      return {
        ...state,
        sortedRooms :tempRms,
        loading: false
      };
    default:
      return state;
  }
};

export default rootReducer;