import React, {useEffect, useState} from "react";
import { connect } from 'react-redux';
import Title from "./Title";
import { FILTERED_ROOMS } from "../types"

// get all unique values
const getUnique = (items, value) => {
  return [...new Set(items.map(item => item[value]))];
};

const RoomsFilter = ({ rooms,getFilteredRoom,...state }) => {

  const {
    type,
    price,
    minPrice,
    maxPrice,
    maxSize
  } = state;

  const  [states, setStates] = useState({
    type: type,
    capacity: "",
    price: maxPrice,
    minSize: "",
    maxSize: maxSize,
    breakfast: "",
    pets: ""
  });

  useEffect(()=> {
    getFilteredRoom(states);
  },[states,rooms,getFilteredRoom]);


  const handleChange = event => {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    setStates({
      ...states,
      [name]: value,
    })

  };

  // get unique types
  let types = getUnique(rooms, "type");

  // add all
  types = ["all", ...types];

  // map to jsx
  types = types.map((item, index) => (
    <option key={index} value={item}>
      {item}
    </option>
  ));

  // get unique capacity
  let people = getUnique(rooms, "capacity");
  people = people.map((item, index) => (
    <option key={index} value={item}>
      {item}
    </option>
  ));

  return (
    <section className="filter-container">
      <Title title="search rooms" />
      <form className="filter-form">
        {/* select type */}
        <div className="form-group">
          <label htmlFor="type">room type</label>
          <select
            name="type"
            id="type"
            onChange={handleChange}
            className="form-control"
           value={states.type}
          >
            {types}
          </select>
        </div>
        {/* end of select type */}

        {/* guests  */}
        <div className="form-group">
          <label htmlFor="capacity">Guests</label>
          <select
            name="capacity"
            id="capacity"
            onChange={handleChange}
            className="form-control"
            value={states.capacity}
          >
            {people}
          </select>
        </div>
        {/* end of guests */}

        {/* room price */}
        <div className="form-group">
          <label htmlFor="price">room price ${price}</label>
          <input
            type="range"
            name="price"
            min={minPrice}
            max={maxPrice}
            id="price"
            value={states.price}
            onChange={handleChange}
            className="form-control"
          />
        </div>
        {/* end of room price*/}

        {/* size */}
        <div className="form-group">
          <label htmlFor="price">room size </label>
          <div className="size-inputs">
            <input
              type="number"
              name="minSize"
              value={states.minSize}
              onChange={handleChange}
              className="size-input"
            />
            <input
              type="number"
              name="maxSize"
             value={states.maxSize}
              onChange={handleChange}
              className="size-input"
            />
          </div>
        </div>
        {/* end of select type */}

        {/* extras */}
        <div className="form-group">
          <div className="single-extra">
            <input
              type="checkbox"
              name="breakfast"
              id="breakfast"
             checked={states.breakfast}
              onChange={handleChange}
            />
            <label htmlFor="breakfast">breakfast</label>
          </div>
          <div className="single-extra">
            <input
              type="checkbox"
              name="pets"
              checked={states.pets}
              onChange={handleChange}
            />
            <label htmlFor="breakfast">pets</label>
          </div>
        </div>
        {/* end of extras type */}
      </form>
    </section>
  );
};

const mapStateToProps = state => ({
  type: state.type,
  price: state.price,
  minPrice: state.minPrice,
  maxPrice: state.maxPrice,
  maxSize: state.maxSize,
});


const mapDispatchToProps = (dispatch) => {
  return  {getFilteredRoom: data => dispatch({ type: FILTERED_ROOMS, payload: data })
  } 
}

export default connect(mapStateToProps,mapDispatchToProps)(RoomsFilter);