import React, { Component } from "react";
import { connect } from 'react-redux';
import defaultBcg from "../images/room-1.jpeg";
//import Hero from "../components/Hero";
import Banner from "../components/Banner";
import { Link } from "react-router-dom";
import { GET_ROOM } from "../types"
import { LazyLoadImage} from "react-lazy-load-image-component"

import StyledHero from "../components/StyledHero";

class SingleRoom extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      slug: this.props.match.params.slug,
      defaultBcg: defaultBcg
    };
  }

  componentDidMount() {
    const { getRoom } = this.props;
    getRoom(this.state.slug);
  }
  render() {

    if (!this.props.room) {
      return (
        <div className="error">
          <h3> no such room could be found...</h3>
          <Link to="/rooms" className="btn-primary">
            back to rooms
          </Link>
        </div>
      );
    }
    const {
      name,
      description,
      capacity,
      size,
      price,
      extras,
      breakfast,
      pets,
      images
    } = this.props.room;
    const [main, ...defaultImages] = images;

    return (
      <>
        <StyledHero img={main || this.state.defaultBcg}>
          <Banner title={`${name} room`}>
            <Link to="/rooms" className="btn-primary">
              back to rooms
            </Link>
          </Banner>
        </StyledHero>
        <section className="single-room">
          <div className="single-room-images">
            {defaultImages.map((item, index) => (
              <LazyLoadImage threshold={[0.5]} effect="blur" key={index} src={item} alt={name} />
            ))}
          </div>
          <div className="single-room-info">
            <article className="desc">
              <h3>details</h3>
              <p>{description}</p>
            </article>
            <article className="info">
              <h3>info</h3>
              <h6>price : ${price}</h6>
              <h6>size : {size} SQFT</h6>
              <h6>
                max capacity :
                {capacity > 1 ? `${capacity} people` : `${capacity} person`}
              </h6>
              <h6>{pets ? "pets allowed" : "no pets allowed"}</h6>
              <h6>{breakfast && "free breakfast included"}</h6>
            </article>
          </div>
        </section>
        <section className="room-extras">
          <h6>extras </h6>
          <ul className="extras">
            {extras.map((item, index) => (
              <li key={index}>- {item}</li>
            ))}
          </ul>
        </section>
      </>
    );
  }
}



const mapStateToProps = state => ({
  room: state.room,
});

const mapDispatchToProps = (dispatch,ownProps) => {
  const slug = ownProps.match.params.slug;
  return  {getRoom: slug => dispatch({ type: GET_ROOM, payload: slug })
  } 
}
export default connect(mapStateToProps,mapDispatchToProps)(SingleRoom);